import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [

    {
      path: '/',
      redirect: { name: 'home' },
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      redirect: { name: 'main' },
      children: [
        {
          path: 'art/:id',
          name: 'art/:id',
          component: () => import(/* webpackChunkName: "about" */ './views/blogDetile.vue'),
        },
        {
          path: 'main',
          name: 'main',
          component: () => import(/* webpackChunkName: "about" */ './views/Main.vue'),
        },
        {
          path: 'about',
          name: 'about',
          component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
        },
        {
          path: 'message',
          name: 'message',
          component: () => import(/* webpackChunkName: "about" */ './views/Message.vue'),
        },
        {
          path: '/sitemap',
          name: 'sitemap',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/Sitemap.vue'),
        },
      ],
    },
    {
      path: '/addblog',
      name: 'addblog',
      component: () => import(/* webpackChunkName: "about" */ './views/AddBlog.vue'),
    },
    {
      path: '/addeveryday',
      name: 'addeveryday',
      component: () => import(/* webpackChunkName: "about" */ './views/AddEveryDay.vue'),
    },
    {
      path: '*',
      name: '*',
      component: () => import(/* webpackChunkName: "about" */ './views/404.vue'),
    },
  ],
});
